
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
mounted() {

},
components: {
XHeader,
},
data() {
return {
noData: false,
list: [],
};
},
mounted() {

this.$refs.scroll.$el.style.height = document.body.clientHeight-74+'px'
this.initData(true);
this.$vux.loading.show({
text: "加载中..."
});
},
methods: {

starActivity(YSID){
this.$router.push({name: "descUseMed", params: {YSID: YSID}});
},

getString(info){
return typeof info !='undefined'&&info!='' ?info : '--';
},
/**
* @param upOrDown
* true 第一次加载或者刷新
* false 上拉加载
*/
initData(upOrDown) {
var myDate = new Date();
this.start = myDate.getFullYear() + "" + (myDate.getMonth() + 1);
if (upOrDown) {
this.pageIndex = 1;
} else {
this.pageIndex++;
}

this.$ajax
.post("", {
"MSH.1": "yesuanService",
"MSH.2": "list",
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM': this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,

})
.then(data => {
this.$vux.loading.hide();
if (typeof data != "undefined" && data.length > 0) {

this.noData = false;
this.list = this.list.concat(data);
} else {
if (this.pageIndex > 1) {
this.$vux.toast.text("没有更多了", "bottom");
this.$refs.scroll.finishPullUp();
} else {
this.noData = true;
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
.catch(error => {
console.log(error);
this.noData = true;
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
});
}
}
};



